.benefits {
    padding-top: 105px;
    background-color: $color-light-grey;

    .container.benefits__single__box__container {
        width: 1124px;
        display: flex;
        flex-direction: column;

        .benefits__single__box {
            height: 370px;
            border: 1px solid #E8E9ED;
            border-radius: 5px;
            background-color: $color-white;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 37px;
            padding-left: 35px;

            .single__box__text {
                h2 {
                    font-family: $font-raleway;
                    font-size: 2.25rem;
                    line-height: 2.625rem;
                    color: $color-black;
                    margin-bottom: 30px;
                }

                p {
                    font-family: $font-open-sans;
                    font-size: 1rem;
                    line-height: 1.375rem;
                    margin-bottom: 50px;
                }
            }

            &:first-child {
                .single__box__text {
                    &:after {
                        content: "";
                        height: 4px;
                        width: 328px;
                        background-color: $color-redish;
                        display: block;
                    }
                }

                .single__box__img {
                    margin-top: 113px;
                    margin-bottom: 38px;
                    margin-right: 118px;
                    position: relative;

                    &:before {
                        content: "";
                        height: 180px;
                        width: 180px;
                        border-radius: 41px;
                        background: $color-redish;
                        transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
                        position: absolute;
                        top: -40px;
                        right: 0;
                        z-index: 0;
                    }
                }
            }

            &:nth-child(2) {
                display: flex;
                flex-direction: row-reverse;
                margin-bottom: 38px;

                .single__box__text {
                    margin-right: 20px;

                    &:after {
                        content: "";
                        width: 328px;
                        height: 4px;
                        background-color: $color-primary;
                        display: block;
                    }
                }

                .single__box__img {
                    position: relative;
                    z-index: 1;
                    margin-top: 29px;
                    margin-left: 35px;

                    &:before {
                        content:"";
                        width: 320px;
                        height: 320px;
                        border-radius: 50%;
                        background: $color-primary;
                        display: block;
                        position: absolute;
                        left: 0;
                        top: -20px;
                        z-index: 0;
                    }

                    img {
                        height: 328px;
                        width: 426px;
                    }
                }
            }

            .single__box__img {
                img {
                    height: 219px;
                    width: 386px;
                    position: relative;
                    z-index: 1;
                }
            }
        }
    }

    .container.benefits__double__box__container {
        .benefits__double__box {
            display: flex;
            margin-bottom: 105px;

            .first__box {
                height: 370px;
                width: 623px;
                border: 1px solid #E8E9ED;
                border-radius: 5px;
                background: $color-white;
                display: flex;
                flex-direction: column;
                justify-content: center;
                padding-left: 36px;

                h2 {
                    font-family: $font-raleway;
                    font-size: 2.25rem;
                    line-height: 2.625rem;
                    color: $color-black;
                    margin-bottom: 30px;
                }

                p {
                    font-family: $font-open-sans;
                    font-size: 1rem;
                    line-height: 1.375rem;
                    color: $color-black;
                    margin-bottom: 44px;
                }

                &:after {
                    content: "";
                    height: 4px;
                    width: 328px;
                    background-color: $color-green;
                    display: block;
                }
            }

            .second__box {
                height: 370px;
                width: 440px;
                border: 1px solid #E8E9ED ;
                border-radius: 5px;
                background: $color-green;
                display: flex;
                justify-content: center;
                align-items: center;
                margin-left: 61px;

                img {
                    width: 295px;
                    margin-top: 55px;
                }
            }
        }
    }
}