.statistics {
    .container.statistics__container {
        h2 {
            font-family: $font-raleway;
            font-size: 2.25rem;
            line-height: 2.625rem;
            color: $color-black;
            text-align: center;
            margin-top: 100px;
        }

        .statistics__box {
            display: flex;
            justify-content: space-evenly;
            margin-top: 100px;

            // @include mobile {
            //     flex-wrap: wrap;
            // }

            .box__element {
                display: flex;
                flex-direction: column;
                align-items: center;
                text-align: center;
                padding-bottom: 121px;

                .box__element__logo {
                    .logo__dot {
                        height: 63px;
                        width: 63px;
                        border-radius: 50%;
                        margin-bottom: 13px;
                        position: relative;
                        z-index: 0;
    
                        img {
                            position: absolute;
                            z-index: 1;
                        }
                    }
                }

                &:first-child {
                    .logo__dot {
                        background-color: $color-redish;

                        img {
                            top: 5px;
                        } 
                    }
                }

                &:nth-child(2) {
                    .logo__dot {
                        background-color: $color-primary;
   
                        img {
                            top: 14px;
                            left: 21px;
                        } 
                    }
                }

                &:last-child {
                    .logo__dot {
                        background-color: $color-green;

                        img {
                            top: 5px;
                        } 
                    }
                }

                .box__element__number {
                    font-family: $font-raleway;
                    font-size: 1.25rem;
                    font-weight: 800;
                    letter-spacing: 0.1875rem;
                    color: $color-black;
                    margin-bottom: 19px;
                }

                .box__element__text {
                    font-family: $font-raleway;
                    font-size: 1.063rem;
                    line-height: 1.25rem;
                    color: $color-grey;
                } 
            }
        }    
    }
}




