.contact {
    .container.contact__container {
        display: flex;
        justify-content: center; 
        margin-bottom: 59px;

        .contact__information__box {
            .contact__information__box__text {
                margin-bottom: 42px;

                h1 {
                    font-family: $font-raleway;
                    font-size: 3.063rem;
                    line-height: 3.563rem;
                    color: $color-black;
                    margin-bottom: 22px;
                }

                span {
                    font-family: $font-open-sans;
                    font-size: 1rem;
                    line-height: 1.375rem;
                    color: $color-black;
                }
            }

            .contact__information {
                .email {
                    text-decoration: none;
                    font-family: $font-open-sans;
                    font-weight: 400;
                    font-size: 1rem;
                    line-height: 1.375rem;
                    color: $color-grey;
                    cursor: pointer;

                    img {
                        height: 27px;
                        width: 27px;
                        margin-right: 10px;
                    }
                }

                .phone__number {
                    font-family: $font-open-sans;
                    font-weight: 400;
                    font-size: 1rem;
                    line-height: 1.375rem;
                    color: $color-grey;
                    text-decoration: none;
                    cursor: pointer;
                    text-align: left;

                    img {
                        width: 27px;
                        height: 27px;
                        margin-right: 8px;
                        margin-left: 18px;
                    }
                }
            }
        }

        .contact__form__box {
            height: 372px;
            width: 441px;
            margin-left: 114px;

            .contact__form__fields {
                .field__name {
                    label {
                        font-family: $font-open-sans;
                        font-size: 0.688rem;
                        font-weight: bold;
                        line-height: 0.938rem;
                        text-transform: uppercase;
                        color: $color-primary;
                    }

                    input {
                        height: 42px;
                        width: 100%;  
                        border-top: none;
                        border-left: none;
                        border-right: none;
                        border-bottom-width: 1px;
                        border-bottom-color: $color-grey;
                        margin-bottom: 42px; 
                    }
                }

                .field__email {
                    label {
                        font-family: $font-open-sans;
                        font-size: 0.688rem;
                        font-weight: bold;
                        line-height: 0.938rem;
                        text-transform: uppercase;
                        color: $color-primary;
                    }

                    input {
                        height: 42px;
                        width: 440px;
                        border-top: none;
                        border-left: none;
                        border-right: none;
                        border-bottom-width: 1px;
                        border-bottom-color: $color-grey;
                        margin-bottom: 30px;
                    }
                }
            }

            .contact__form__checkbox {
                .checkbox {
                    display: flex;
                    cursor: pointer;

                    input {
                        height: 0;
                        width: 0;
                        position: absolute;
                        opacity: 0;
                        cursor: pointer;

                        &:checked {
                            + span {
                                background-image: url(../assets/Checkmark.svg);
                                background-size: 50%;
                                background-repeat: no-repeat;
                                background-position: center;
                            }   
                        }
                    }

                    span {
                        height: 24px;
                        width: 24px;
                        border: 1px solid $color-grey;
                        border-radius: 4px;
                        background: $color-white;
                    }

                    .checkbox__text {
                        font-family: $font-open-sans;
                        font-size: 0.75rem;
                        line-height: 1.063rem;
                        color: $color-grey;
                        margin-bottom: 20px;
                        margin-left: 10px;
                    }
                }
            }

            .contact__form__btn {
                display: flex;
                justify-content: flex-end;

                .btn {
                    height: 57px;
                    width: 226px;
                    background: $color-primary;
                    box-shadow: 5px 10px 14px $color-primary-shadow;
                    border-radius: 35px;
                    border: none;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    cursor: pointer;
                }

                .contact__form__btn__text {
                    text-decoration: none;
                    font-family: $font-open-sans;
                    font-size: 0.813rem;
                    font-weight: bold;
                    color: $color-white;
                }
            }
        }
    }
}



