.prices {
    h2 {
        font-family: $font-raleway;
        font-size: 2.25rem;
        line-height: 2.625rem;
        color: $color-black;
        text-align: center;
        margin-top: 100px;
        margin-bottom: 50px;
    }

    .container.prices__boxes__container {
        display: flex;
        flex-direction: row;
        justify-content: space-around;

        .basic__box {
            height: 461px;
            width: 372px;  
            border: 1px solid $color-redish;
            border-radius: 18px;
            background: $color-white;
            text-align: center;
            margin-top: 19px;

            .basic__box__text {
                display: flex;
                flex-direction: column;
                align-items: center;
                text-align: center;
                margin-top: 34px;
                margin-bottom: 60px;

                .offer__type {
                    font-family: $font-raleway;
                    font-size: 1.25rem;
                    font-weight: 700;
                    margin-bottom: 25px;
                }

                .price {
                    font-family: $font-open-sans;
                    font-size: 2.875rem;
                    font-weight: 300;
                    color: $color-redish;
                    margin-top: 25px;
                }
            }

            .offer__details {
                .element__yes {
                    font-family: $font-open-sans;
                    font-size: 0.875rem;
                    line-height: 2.563rem;

                    &:before {
                        content: "";
                        background-image: url("../assets/Checkmark-1.svg");
                        height: 11px;
                        width: 11px;
                        display: inline-block;
                        margin-right: 10px;
                    }
                }

                .element__no {
                    font-family: $font-open-sans;
                    font-size: 0.875rem;
                    line-height: 2.563rem;

                    &:before {
                        content: "";
                        background-image: url("../assets/Close.svg");
                        height: 11px;
                        width: 11px;
                        display: inline-block;
                        margin-right: 10px;
                    }
                }
            }
            
            .btn {
                height: 50px;
                width: 150px;
                border-radius: 23px;
                background: $color-redish;
                display: flex;
                justify-content: center;
                align-items: center;
                position: relative;
                bottom: -45px;
                right: -112px;
                cursor: pointer;
            }

            .basic__box__btn {
                text-decoration: none;
                font-family: $font-open-sans;
                font-size: 0.813rem;
                font-weight: bold;
                color: $color-white;
            }
        }

        .professional__box {
            height: 500px;
            width: 405px;
            border: 1px solid $color-primary;
            border-radius: 18px;
            background: $color-white;
            box-shadow: 5px 10px 27px rgba(7,172,230, 0.2);
            margin-bottom: 179px;
        
            .professional__box__text {
                display: flex;
                flex-direction: column;
                text-align: center;
                margin-top: 34px;
                margin-bottom: 60px;

                .offer__type {
                    font-family: $font-raleway;
                    font-size: 1.25rem;
                    font-weight: 700;
                }

                .price {
                    font-family: $font-open-sans;
                    font-size: 2.875rem;
                    font-weight: 300;
                    color: $color-primary;
                    margin-top: 25px;
                }

                .limit {
                    font-family: $font-raleway;
                    font-size: 0.75rem;
                    font-weight: 700;
                    margin-top: 15px;
                }
            }

            .offer__details {
                text-align: center;

                .element__yes {
                    font-family: $font-open-sans;
                    font-size: 0.875rem;
                    line-height: 2.563rem;

                    &:before {
                        content: "";
                        background-image: url("../assets/Checkmark-1.svg");
                        height: 11px;
                        width: 11px;
                        display: inline-block;
                        margin-right: 10px;
                    }
                }

                .element__no {
                    font-family: $font-open-sans;
                    font-size: 0.875rem;
                    line-height: 2.563rem;

                    &:before {
                        content: "";
                        background-image: url("../assets/Close.svg");
                        height: 11px;
                        width: 11px;
                        display: inline-block;
                        margin-right: 10px;
                    }
                }
            }

            .btn {
                height: 50px;
                width: 165px;
                background: $color-primary;
                box-shadow: 5px 10px 14px $color-primary-shadow;
                border-radius: 23px;
                display: flex;
                justify-content: center;
                align-items: center;
                position: relative;
                bottom: -57px;
                right: -117px;
                cursor: pointer;
            }

            .professional__box__btn {
                text-decoration: none;
                font-family: $font-open-sans;
                font-size: 0.813rem;
                font-weight: bold;
                color: $color-white;
            }
        }
        
        .premium__box {
            height: 461px;
            width: 372px;
            border: 1px solid $color-green; 
            border-radius: 18px;
            background: $color-white;
            text-align: center;
            margin-top: 19px;

            .premium__box__text {
                display: flex;
                flex-direction: column;
                text-align: center;
                margin-top: 34px;
                margin-bottom: 60px;

                .offer__type {
                    font-family: $font-raleway;
                    font-size: 1.25rem;
                    font-weight: 700;
                }

                .price {
                    font-family: $font-open-sans;
                    font-size: 2.875rem;
                    font-weight: 300;
                    color: $color-green;
                    margin-top: 25px;
                }

                .limit {
                    font-family: $font-raleway;
                    font-size: 0.75rem;
                    font-weight: 700;
                    margin-top: 15px;
                }
            }

            .offer__details {
                .element__yes {
                    font-family: $font-open-sans;
                    font-size: 0.875rem;
                    line-height: 2.563rem;

                    &:before {
                        content: "";
                        background-image: url("../assets/Checkmark-1.svg");
                        height: 11px;
                        width: 11px;
                        display: inline-block;
                        margin-right: 10px;
                    }
                }

                .element__no {
                    font-family: $font-open-sans;
                    font-size: 0.875rem;
                    line-height: 2.563rem;

                    &:before {
                        content: "";
                        background-image: url("../assets/Close.svg");
                        height: 11px;
                        width: 11px;
                        display: inline-block;
                        margin-right: 10px;
                    }
                }
            }

            .btn {
                height: 50px;
                width: 150px;
                border-radius: 23px;
                background: $color-green;
                display: flex;
                justify-content: center;
                align-items: center;
                position: relative;
                bottom: -19px;
                right: -112px;
                cursor: pointer;
            }

            .premium__box__btn {
                text-decoration: none;
                font-family: $font-open-sans;
                font-size: 0.813rem;
                font-weight: bold;
                color: $color-white;
            }
        }
    }
}


