.header {
    height: 70px;
    width: 100%;
    background: $color-white;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    display: flex;
    align-items: center;

    .container.header__container {
        display: flex;
        justify-content: space-between;

        .header__logo {
            font-family: $font-raleway;
            font-size: 2rem;
            font-weight: 300;
            text-decoration: none;
            color: $color-primary;
        }
    
        .header__nav {
            display: flex;
            align-items: center;
    
            .header__nav__list {
                display: flex;
                flex-wrap: wrap;
    
                .nav__list__element {
                    font-family: $font-raleway; 
                    font-size: 0.6875rem;
                    font-weight: 700;
                    letter-spacing: 0.1875rem;
                    text-transform: uppercase;
                    text-decoration: none;
                    color: $color-grey;
                    cursor: pointer;
                    margin-left: 95px;
    
                    &:hover {
                        color: $color-black;
                        padding-bottom: 23px;
                        border-bottom: 4px solid $color-primary;
                    }
                }
            }
        }
    } 
}