.opening {
    display: flex;
    flex-wrap: wrap;

    .container.opening__container {
        .opening__text {
            display: flex;
            flex-direction: column;
            align-items: center;

            h1 {
                font-family: $font-raleway;
                font-size: 3.063rem;
                line-height: 3.563rem;
                margin-top: 130px;
                margin-bottom: 20px;
            }

            span {
                font-size: 1rem;
                line-height: 1.375rem;
                color: $color-black;
                margin-bottom: 38px;
            }

            .opening__btn {
                height: 67px;
                width: 440px;
                background: $color-primary;
                box-shadow: 5px 10px 14px #07ACE629;
                border-radius: 40px;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;

                .btn {
                    font-size: 1rem;
                    font-weight: bold;
                    line-height: 1.375rem;
                    text-decoration: none;
                    color: $color-white;
                    text-align: center;
                }
            }
        
        }

        .opening__img {
            img {
                height: auto;
                width: 100%;  
            }
        }
    }
}



