.footer {
    box-shadow: 0 -5px 40px #00000029;

    .container.footer__container {
        height: 89px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .footer__information__box {
            display: flex;
            flex-direction: column;

            .footer__logo {
                font-family: $font-raleway;
                font-size: 1.625rem;
                font-weight: 300;
                line-height: 1.875rem;
                text-decoration: none;
                color: $color-primary;
                margin-bottom: 7px;
            }

            .footer__copyright {
                font-family: $font-open-sans;
                font-size: 0.688rem;
                line-height: 0.938rem;
                color: $color-grey;
            }
        }

        .footer__social__media__box {
            .twitter {
                width: 24px;
                height: 24px;
                text-decoration: none;
                margin-right: 20px;
            }

            .facebook {
                width: 24px;
                height: 24px;
                text-decoration: none;
            } 
        }
    }
}




